<template>
  <navigation-bar>
    <div slot="middle_content">
      我的订单
    </div>
  </navigation-bar>
  <div class="page">
    <div class="bd">
      <div class="weui_cells_title">带说明的列表项</div>
      <div class="weui_cells">
        <div class="weui_cell">
          <div class="weui_cell_bd weui_cell_primary">
            <p>编号</p>
          </div>
          <div class="weui_cell_ft">{{order.sn}}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        order: {}
      }
    },
    route: {
      data (transition) {
        this.$http.get('http://jiancan.me/api/u1/orders/one.json', { order_id: this.$route.params.order_id }).then(function (response) {
          transition.next({
            order: response.data
          })
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    }
  }
</script>



